
html,body{margin:0 0;padding:0 0;width:100%;height:100%;font-family:cursive,"SimHei",sans-serif;font-size:14px;background-color:#eee;overflow:hidden
}
html ::scrollbar,body ::scrollbar{width:0;height:0
}
html ::-webkit-scrollbar,body ::-webkit-scrollbar{width:0;height:0
}
html #app,body #app{box-sizing:border-box;-webkit-box-sizing:border-box;position:relative;width:100%;height:100%;background:#1f8dd6;border-left:5px dashed #fff;border-right:5px solid transparent;overflow-x:hidden;overflow-y:auto;z-index:999
}
html #app .transition-animation-enter-active,body #app .transition-animation-enter-active{animation:bounce-in .4s
}
html #app .transition-animation-leave-active,body #app .transition-animation-leave-active{animation:bounce-out .4s
}
@keyframes bounce-in{
0%{transform:rotateY(90deg)
}
100%{transform:rotateY(0)
}
}
@keyframes bounce-out{
0%{opacity:1
}
100%{opacity:0
}
}
html #app .app-container,body #app .app-container{width:auto;height:100%
}
html #bg,body #bg{position:fixed;top:0;left:0;display:none;width:100%;height:100%;background-color:#fff;transform:rotate(120deg);-webkit-transform:rotate(120deg);z-index:99
}
@media screen and (min-width:1024px){
html,body{width:100%;height:100%
}
html #app,body #app{position:relative;width:700px;height:90%;margin:2.5% auto;border-left:10px dashed #fff;border-radius:0 2% 2% 0;box-shadow:0 0 10px #888;z-index:999
}
html #bg,body #bg{display:block
}
}


