
#control{width:100%;height:100%
}
#control .controlForm{padding:.4rem .8rem .8rem .8rem
}
#control .controlForm .item{padding:.4rem .8rem;margin:.8rem 0;background-color:#fff;font-size:1.2rem
}
#control .controlForm .item.warn{background-color:#eee
}
#control .controlForm .item.warn p{font-family:cursive,"SimHei",sans-serif
}
#control .controlForm .item .item-label{display:inline-table;width:35%;vertical-align:top
}
#control .controlForm .item .item-input{display:inline-table;width:65%
}
#control .controlForm .item .item-input input{padding:.25rem 0;height:100%;width:100%;border:none;background-color:#fff;font-family:cursive,"SimHei",sans-serif;font-size:1rem;line-height:1.2rem
}
#control .controlForm .item .item-input input:focus,#control .controlForm .item .item-input input:active{outline:none
}
#control .controlForm .item .item-input input:disabled{background-color:#fff;color:#888
}
#control .controlForm .item .item-input textarea{width:100%;font-family:cursive,"SimHei",sans-serif;font-size:1rem;line-height:1.2rem;border:none;resize:none
}
#control .controlForm .item .item-input textarea:focus,#control .controlForm .item .item-input textarea:active{outline:none
}
@media screen and (max-width:480px){
#control .controlForm .item .item-label{display:block;margin-bottom:8px;width:100%
}
#control .controlForm .item .item-input{display:block;width:100%
}
}
#control .controlForm .control-item{margin-top:3rem;width:100%
}
#control .controlForm .control-item .btn{width:50%;padding:1rem 0;text-align:center;border:none;font-family:cursive,"SimHei",sans-serif;font-size:1.2rem
}
#control .controlForm .control-item .btn.add{color:#000;background-color:#eee
}
#control .controlForm .control-item .btn.cancel{color:#000;background-color:#fff
}
#control .complateInfo{padding:1rem .8rem;background-color:#fff
}


