
#top{width:100%;height:100%
}
#top #topList{padding:.8rem
}
#top #topList ul{margin:0 0;padding:0 0
}
#top #topList ul li.list-item{padding:1rem;margin:.3rem 0;list-style:none;background-color:#fff;overflow:hidden
}
#top #topList ul li.list-item div{display:inline-block;padding:.5rem 0;width:33.3%;float:left;font-size:1.2rem;text-align:center;white-space:nowrap;overflow:hidden;text-overflow:ellipsis
}
#top #topList ul li.list-item.list-enter-active,#top #topList ul li.list-item.list-leave-active{transition:all .8s
}
#top #topList ul li.list-item.list-enter,#top #topList ul li.list-item.list-leave-active{opacity:0;transform:rotateY(90deg)
}


