
#about{width:100%;height:100%
}
#about #about-info{position:relative;margin:5rem .8rem .8rem 1rem;padding:3rem 1rem 1rem 1rem;height:50%;font-size:1.4rem;line-height:2rem;text-indent:2rem;background-color:#fff;border-top-left-radius:10px 60px;border-top-right-radius:160px 25px;border-bottom-right-radius:50px 10px;border-bottom-left-radius:5px 100px
}
#about #about-info::before{content:'';position:absolute;top:.8rem;left:.8rem;width:10px;height:10px;background:#1f8dd6;border-radius:100%
}
#about #about-info::after{content:'';position:absolute;bottom:.8rem;right:.8rem;width:10px;height:10px;background:#1f8dd6;border-radius:100%
}


