
#result{padding:.6rem .8rem;width:auto;height:65%;background-color:#fff;border-radius:5px;text-align:left;overflow:auto
}
#result ul.infoUl{margin:0 0;padding:0 0
}
#result ul.infoUl li{list-style:none
}
#result ul.infoUl li p{margin:1.2rem 0;font-size:1.1rem;line-height:1.8rem
}
#result ul.infoUl li p.auth{text-align:right
}
#result ul.infoUl li p.auth a{color:#1f8dd6;text-decoration:none
}
#result ul.infoUl li p.auth a:hover,#result ul.infoUl li p.auth a:active,#result ul.infoUl li p.auth a:focus{color:#1f8dd6
}
#result p.topBar{margin:.4rem 0;overflow:hidden
}
#result p.topBar a{font-size:1.2rem;line-height:1.2rem;font-family:cursive,"SimHei",sans-serif
}
#result p.topBar a.back{float:right
}
#result p.topBar a.edit{float:left;color:#000
}
#result .noDataInfo{font-size:1.1rem;text-align:center
}
#result .noDataInfo .addLink{margin-top:1rem
}
#result .noDataInfo .addLink a{margin:0 5px;font-weight:bold;color:#1f8dd6;text-decoration:none
}
@media screen and (min-width:480px){
#result{padding:1rem 2rem
}
}


